body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
  background: #e8eaf6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; 
}

html {
  box-sizing: border-box;
  font-size: 62.5%; 
}

figure {
  border: 3px solid black;
  background-repeat: no-repeat;
  background-size: cover;
}

body {
  font-family: "Nunito", sans-serif;
  color: #333;
  font-weight: 300;
  line-height: 1.6; 
}

.whole-thing {
  display: flex;
  height: 800px;
  padding-top: 20px;
}

.search-photos{
  width: 40%;
  margin-left: 20px;
  margin-right: 20px;
}

.background {
  color: #504e4e;
  font-weight: 500;
}

.photos-results {
  height: 80%;
  overflow: scroll;
}
.container {
  width: 60%;
  margin: 2rem auto; 
  margin-right: 10px;
}

.gallery {
  grid-gap: 0;
  display: grid;
  /* grid-gap: 10px 15px; */
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(12, 4vw);
}

.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block; 
}

.gallery__item--1 {
  grid-column-start: 1;
  grid-column-end: 6;
  grid-row-start: 1;
  grid-row-end: 5;
}

.gallery__item--2 {
  grid-column-start: 5;
  grid-column-end: 10;
  grid-row-start: 1;
  grid-row-end: 5;
}

.gallery__item--3 {
  grid-column-start: 9;
  grid-column-end: 14;
  grid-row-start: 1;
  grid-row-end: 8;
}

.gallery__item--4 {
  grid-row-start: 5;
  grid-row-end: 9;
  grid-column-start: 1;
  grid-column-end: 6;
}

.gallery__item--5 {
  grid-row-start: 5;
  grid-row-end: 9;
  grid-column-start: 5;
  grid-column-end: 10;
}

.gallery__item--6 {
  grid-row-start: 9;
  grid-row-end: 13;
  grid-column-start: 1;
  grid-column-end: 6;
}

.gallery__item--7 {
  grid-row-start: 9;
  grid-row-end: 13;
  grid-column-start: 5;
  grid-column-end: 10;
}

img {
  margin: 5px;
}

a {
  margin-right: 20px;
}

.collageHeader > h1 {
  font-weight: 700;
}

.dropdown-content li>a, .dropdown-content li>span, a.collage-btn {
  color:#474950;
}

a.collage-btn {
  margin-top: 10px;
  margin-right: 0;
}

#saveCollage {
  padding: 10px 0px 20px 0px;
}

div.background-color {
    margin-top: 10px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    height: 54px;
}

.gallery__item--8 {
  grid-row-start: 8;
  grid-row-end: 13;
  grid-column-start: 9;
  grid-column-end: 13;
}

.center-align {
  text-align: center;
  font-size: x-large;
}

.top-align, .bottom-align {
  text-align: center;
}

.text-center {
  border: none;
}

.row {
  margin-bottom: 0px;
}

.instructions-drag-n-drop{
  text-align: center;
}

.header {
  font-weight: 500;
  font-family: 'Abel';
}

.adjustment {
  color: #797878;
  font-size: small;
  font-weight: 400;
  background: #d6d6d66e;
  border-radius: 10px;
  padding: 10px;
}